@use "sass:math";
.section-kv {
    position: relative;
    background: #000 url('../images/kv-bg-v2.jpg') no-repeat center top;
    background-size: auto 100%;
    overflow: hidden;
    padding-bottom: 50%;
    @include xs {
        background-image: url('../images/kv-bg-m-v2-3@.jpg');
        padding-bottom: 0;
        min-height: calc(var(--vh, 1vh) * 100);
        background-size: 105% auto;
        background-position: center bottom;
    }
    &__content {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50.5%,-43.9%,0);
        z-index: 10;
        @include xs {
            top: inherit;
            bottom: 0;
            transform: translate3d(-50%,-24%,0);
        }
    }
    &__main-title {
        width: calc((1020 / 1920) * 100vw);
        margin: 0 auto;
        position: relative;
        z-index: 0;
        @include xs {
            width: 72vw;
            transform: translate3d(2%,0,0);
        }
        img {
            position: relative;
            z-index: 0;
            @extend %rsp-img;
        }
    }
    &__sub-title {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-35%,108%,0);
        width: calc((760 / 1920) * 100vw);
        margin: 0 auto;
        @include xs {
            transform: translate3d(-52%,170%,0);
            width: 90vw;
        }
        img {
            position: relative;
            z-index: 0;
            @extend %rsp-img;
        }
    }
    &__btn-play {
        cursor: pointer;
        width: calc((145 / 1920) * 100vw);
        height: calc((146 / 1920) * 100vw);
        background: transparent url('../images/kv-btn-play@2x.png') no-repeat center bottom;
        background-size: cover;
        position: absolute;
        z-index: 10;
        top: 0;
        left: 50%;
        transform: translate3d(-50%,160%,0) rotate(0);
        transition: transform 300ms ease;
        &:hover {
            transform: translate3d(-50%,160%,0) rotate(360deg);
        }
        @include xs {
            width: calc((145 / 414) * 50vw);
            height: calc((146 / 414) * 50vw);
            transform: translate3d(-50%,200%,0) rotate(0);
            &:hover {
                transform: translate3d(-50%,200%,0) rotate(0);
            }
        }
    }
    &__btn-event {
        transform: translate3d(0,10%,0);
        width: calc((547 / 1920) * 100vw);
        height: calc((164 / 1920) * 100vw);
        background: transparent url('../images/kv-btn-event-v2@2x.png') no-repeat center bottom;
        background-size: auto 100%;
        display: flex;
        margin: 0 auto;
        border: 0;
        padding: 0;
        font-size: 0;
        transition: transform 300ms ease;
        &:hover {
            transform: translate3d(0,12%,0);
        }
        @include xs {
            width: calc((547 / 414) * 50vw);
            height: calc((164 / 414) * 50vw);
            transform: translate3d(0,60%,0);
            &:hover {
                transform: translate3d(0,60%,0);
            }
        }
    }
}