.site-header {
    background-color: #070707;
    height: calc((74 / 1920) * 100vw);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    @include xs {
        height: calc((60 / 414) * 100vw);
        background-color: rgba(0,0,0,0.7);
    }
    &__inner {
        width: calc((1800 / 1920) * 100vw);
        margin: 0 auto;
        height: 100%;
        @include xs {
            width: 100%;
            padding-left: 2.4rem;
            padding-right: 2.4rem;
            justify-content: flex-end;
        }
        @include sm {
            width: 100%;
            padding-left: 2.4rem;
            padding-right: 2.4rem;
        }
    }
    &__btn-close {
        width: 6vw;
        height: 6vw;
        background-color: transparent;
        z-index: 15;
        position: absolute;
        border: 0;
        top: 4vw;
        font-size: 0;
        right: 6vw;
        display: none;
        &::before,
        &::after {
            width: 100%;
            height: 0.3rem;
            background-color: #fff;
            position: absolute;
            content: "";
            left: 50%;
            top: 50%;
        }
        &::before {
            transform: translate3d(-50%,-50%,0) rotate(-45deg);
        }
        &::after {
            transform: translate3d(-50%,-50%,0) rotate(45deg);
        }
        @include xs {
            display: block;
            opacity: 0;
            transition: opacity 900ms ease;
            &.active {
                opacity: 1;
            }
        }
    }
    &__btn-open {
        width: 6vw;
        height: 5vw;
        background-color: transparent;
        z-index: 5;
        position: absolute;
        border: 0;
        top: 4.5vw;
        font-size: 0;
        left: 7.5vw;
        display: none;
        &::before,
        &::after {
            width: 100%;
            height: 0.3rem;
            background-color: #979797;
            position: absolute;
            content: "";
            left: 50%;
        }
        &::before {
            transform: translate3d(-50%,0,0);
            top: 0;
        }
        &::after {
            transform: translate3d(-50%,0,0);
            bottom: 0;
        }
        span {
            width: 100%;
            height: 0.3rem;
            background-color: #979797;
            position: absolute;
            top: 50%;
            transform: translate3d(0,-50%,0);
            left: 0;
        }
        @include xs {
            display: block;
        }
    }
    &__odin {
        width: calc((84 / 1920) * 100vw);
        height: calc((38 / 1920) * 100vw);
        background: transparent url('../images/odin-logo@3x.png') no-repeat 0 0;
        background-size: cover;
        position: absolute;
        top: 50%;
        left: calc((30 / 1920) * 100vw);
        transform: translate3d(0,-50%,0);
        @include xs {
            width: calc((84 / 414) * 100vw);
            height: calc((38 / 414) * 100vw);
            left: 50%;
            top: 15vw;
            transform: translate3d(-50%,-300%,0);
            z-index: 15;
            opacity: 0;
            transition: all 800ms ease;
            &.active {
                opacity: 1;
                transform: translate3d(-50%,0,0);
            }
        }
    }
    &__nav {
        width: 100%;
        justify-content: center;
        display: flex;
        margin: 0 auto;
        height: 100%;
        @include xs {
            justify-content: start;
            box-sizing: border-box;
            position: fixed;
            flex-direction: column;
            width: 100%;
            height: 100vh;
            padding-top: 40vw;
            background-color: #000;
            top: 0;
            left: 0;
            margin-right: 0;
            transform: translate3d(0,-200%,0);
            z-index: 10;
            transition: transform 800ms ease;
            &.active {
                transform: translate3d(0,0,0);
            }
        }
    }
    &__btn {
        font-family: 'Noto Sans TC', sans-serif;
        color: #fff;
        line-height: 1.4;
        text-align: center;
        display: flex;
        align-items: center;
        padding: 0 calc((62 / 1920) * 100vw);
        font-size: calc((16 / 1920) * 100vw);
        transition: background-color 300ms ease;
        height: 100%;
        &:hover {
            background-color: #bb4d32;
            @include xs {
                background-color: transparent;
            }
        }
        &.active {
            background-color: #bb4d32;
        }
        @include xs {
            height: 8rem;
            font-size: 4vw;
            justify-content: center;
            br {
                display: none;
            }
        }
        @include sm {
            padding-left: 2vw;
            padding-right: 2vw;
        }
    }
    &__link {
        position: absolute;
        right: calc((30 / 1920) * 100vw);
        display: flex;
        align-items: center;
        top: 50%;
        transform: translate3d(0,-50%,0);
    }
    &__btn-link {
        font-size: calc((14 / 1920) * 100vw);
        color: #7e7e7e;
        padding: 0 calc((18 / 1920) * 100vw);
        position: relative;
        transition: color 300ms ease;
        @include xs {
            font-size: 3.4vw;
            padding-left: 2vw;
            padding-right: 2vw;
        }
        &:hover {
            color: #fff;
        }
        &:nth-child(1) {
            &::after {
                width: calc((2 / 1920) * 100vw);
                background-color: #7e7e7e;
                height: calc((12 / 1920) * 100vw);
                position: absolute;
                right: calc((1 / 1920) * -100vw);
                top: 50%;
                transform: translate3d(0,-50%,0);
                content: "";
                position: absolute;
                @include xs {
                    width: 0.1rem;
                    height: 2vw;
                }
            }
        }
    }
}