.site-footer {
    padding: calc((40 / 1920) * 100vw) 0;
    background-color: #000;
    @include xs {
        padding-top: 8vw;
        padding-bottom: 8vw;
    }
    &__inner {
        width: calc((700 / 1920) * 100vw);
        margin: 0 auto;
        display: flex;
        gap: calc((10 / 1920) * 100vw);
        @include xs {
            width: 100%;
            padding-left: 4vw;
            padding-right: 4vw;
            gap: 2vw;
        }
        @include sm {
            width: 60vw;
            gap: 2vw;
        }
    }
    &__level {
        width: calc((56 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 14vw;
        }
        @include sm {
            width: 10vw;
        }
    }
    &__content {
        ul {
            li {
                font-size: calc((13 / 1920) * 100vw);
                color: #777676;
                line-height: 1.6;
                @include xs {
                    font-size: 1.5vw;
                }
            }
        }
        p {
            font-size: calc((13 / 1920) * 100vw);
            color: #777676;
            line-height: 1.6;
            @include xs {
                font-size: 2vw;
            }
        }
    }
}