.section-battlefield {
    $self: &;
    position: relative;
    background: #000 url('../images/battlefield-bg.jpg') no-repeat center top;
    background-size: auto 100%;
    padding-bottom: 50%;
    overflow: hidden;
    @include xs {
        background-image: url('../images/battlefield-bg-m-v2.jpg');
        padding-bottom: 0;
        min-height: calc(var(--vh, 1vh) * 100);
        background-size: 120% auto;
        background-position: center 40%;
    }
    &__content {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-50%,0);
        z-index: 10;
        @include xs {
            transform: translate3d(-50%,-50%,0);
        }
    }
    &__main-title {
        width: calc((663 / 1920) * 100vw);
        margin: 0 auto calc((40 / 1920) * 100vw);
        position: relative;
        z-index: 10;
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 90vw;
            margin-bottom: 16vw;
        }
    }
    &__slider {
        position: relative;
        z-index: 0;
    }
    .swiper {
        width: calc((1180 / 1920) * 100vw);
        position: relative;
        margin-bottom: calc((24 / 1920) * 100vw);
        .swiper-slide {
            width: calc((832 / 1920) * 100vw);
            height: calc((485 / 1920) * 100vw);
            @include xs {
                width: 100%;
                height: auto;
            }
            @include sm {
                height: auto;
            }
            #{$self}__slide-content {
                filter: brightness(0.4);
            }
        }
        .swiper-slide-active {
            #{$self}__slide-content {
                filter: brightness(1);
            }
        }
        @include xs {
            width: 96vw;
            height: 56vw;
            margin-left: auto;
            margin-right: auto;
        }
        @include sm {
            width: 60vw;
        }
    }
    &__slide-content {
        margin: 0 auto;
        overflow: hidden;
        position: relative;
        img {
            @extend %rsp-img;
            position: relative;
            z-index: 0;
        }
    }
    &__intro {
        width: 100%;
        position: absolute;
        z-index: 10;
        bottom: calc((20 / 1920) * 100vw);
        left: 50%;
        transform: translate3d(-50%,0,0);
        @include xs {
            bottom: 2vw;
        }
        h3 {
            font-size: calc((26 / 1920) * 100vw);
            line-height: 1.6;
            color: #fada9c;
            text-align: center;
            @include xs {
                font-size: 3vw;
            }
        }
        p {
            font-size: calc((16 / 1920) * 100vw);
            line-height: 1.6;
            color: #fff;
            text-align: center;
            @include xs {
                font-size: 2.6vw;
            }
        }
        small {
            padding-top: calc((8 / 1920) * 100vw);
            font-size: calc((16 / 1920) * 100vw);
            color: #b7ad6e;
            text-align: center;
            padding-left: calc((20 / 1920) * 100vw);
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: calc((8 / 1920) * 100vw);
            @include xs {
                font-size: 2.5vw;
            }
            img {
                width: calc((16 / 1920) * 100vw);
                height: calc((16 / 1920) * 100vw);
                @include xs {
                    width: 2.6vw;
                    height: 2.6vw;
                }
            }
        }
    }
    .battlefield-swiper-button-prev {
        width: calc((63 / 1920) * 100vw);
        height: calc((85 / 1920) * 100vw);
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate3d(180%,-90%,0) !important;
        z-index: 30;
        background: transparent url('../images/update-btn-prev@2x.png') no-repeat 0 0;
        background-size: cover;
        cursor: pointer;
        @include xs {
            width: calc((63 / 414) * 50vw);
            height: calc((85 / 414) * 50vw);
            left: 50%;
            transform: translate3d(-50%,-380%,0) rotate(90deg) !important;
        }
    }
    .battlefield-swiper-button-next {
        width: calc((63 / 1920) * 100vw);
        height: calc((85 / 1920) * 100vw);
        position: absolute;
        right: 0;
        top: 50%;
        transform: translate3d(-180%,-90%,0) !important;
        z-index: 30;
        background: transparent url('../images/update-btn-next@2x.png') no-repeat 0 0;
        background-size: cover;
        cursor: pointer;
        @include xs {
            width: calc((63 / 414) * 50vw);
            height: calc((85 / 414) * 50vw);
            right: inherit;
            left: 50%;
            transform: translate3d(-50%,300%,0) rotate(90deg) !important;
        }
    }
    .battlefield-swiper-button-disabled {
        opacity: 0.3;
        pointer-events: none;
    }
    .battlefield-swiper-pagination {
        display: flex;
        justify-content: center;
        padding: calc((48 / 1920) * 100vw) 0;
        gap: 0;
        @include xs {
            padding-top: 5vw;
            padding-bottom: 12vw;
        }
    }
    .battlefield-swiper-pagination-bullet {
        padding: 0 calc((35 / 1920) * 100vw);
        font-size: calc((26 / 1920) * 100vw);
        color: #fff;
        background-size: calc((355 / 1920) * 100vw) auto;
        position: relative;
        cursor: pointer;
        display: flex;
        align-items: flex-end;
        @include xs {
            font-size: calc((12 / 414) * 100vw);
            padding-left: 2vw;
            padding-right: 2vw;
        }
        span {
            background-image: url('../images/update-text-texture@2x.jpg');
            background-color: transparent;
            background-repeat: repeat;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-size: calc((22 / 1920) * 100vw);
            padding-left: calc((4 / 1920) * 100vw);
            @include xs {
                font-size: calc((13 / 414) * 100vw);
                position: relative;
                top: 0.2vw;
            }
            strong {
                font-size: calc((28 / 1920) * 100vw);
                @include xs {
                    font-size: calc((16 / 414) * 100vw);
                }
            }
        }
        &::before {
            pointer-events: none;
            bottom: 0;
            left: 50%;
            transform: translate3d(-50%,120%,0);
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            background: transparent url('../images/update-pagination@2x.png') no-repeat center bottom;
            background-size: calc((355 / 1920) * 100vw) auto;
            @include xs {
                background-size: calc((355 / 414) * 40vw) auto;
                transform: translate3d(-50%, 100%, 0);
            }
        }
        &::after {
            pointer-events: none;
            bottom: 0;
            left: 50%;
            transform: translate3d(-50%,54.5%,0);
            position: absolute;
            content: "";
            width: calc((363 / 1920) * 100vw);
            height: calc((348 / 1920) * 100vw);
            background: transparent url('../images/update-pagination-active@2x.png') no-repeat center bottom;
            background-size: calc((363 / 1920) * 100vw) auto;
            transition: opacity 300ms ease;
            opacity: 0;
            @include xs {
                width: calc((363 / 414) * 50vw);
                height: calc((348 / 414) * 50vw);
                transform: translate3d(-50%,25%,0);
            }
            @include sm {
                transform: translate3d(-50%,56%,0);
            }
        }
    }
    .battlefield-swiper-pagination-bullet-active {
        &::after {
            opacity: 1;
        }
    }
    &__custom-pagination {
        margin: 0 auto;
        width: calc((267 / 1920) * 100vw);
        height: calc((40 / 1920) * 100vw);
        background: transparent url('../images/custom-pagination-bg@2x.png') no-repeat center center;
        background-size: auto calc((20 / 1920) * 100vw);
        display: flex;
        justify-content: space-between;
        @include xs {
            display: none;
        }
        span {
            width: calc((40 / 1920) * 100vw);
            height: calc((40 / 1920) * 100vw);
            background: transparent url('../images/custom-pagination-active@2x.png') no-repeat center top;
            background-size: cover;
            display: block;
            opacity: 0;
            cursor: pointer;
            transition: opacity 300ms ease;
            &.active {
                opacity: 1;
            }
        }
    }
}