:root {
    font-size: 10px;
    --font-size-6xl: 6.4rem;
    --font-size-5xl: 4.8rem;
    --font-size-4xl: 3.6rem;
    --font-size-3xl: 3rem;
    --font-size-2xl: 2.4rem;
    --font-size-xl: 2rem;
    --font-size-lg: 1.8rem;
    --font-size-base: 1.6rem;
    --font-size-sm: 1.4rem;
    --font-size-xs: 1.2rem;
}

html {
    font-family: 'Roboto', 'Noto Sans TC', "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    &.fixed {
        overflow: hidden;
    }
}

body {
    background-color: #000;
    &.fixed {
        overflow: hidden;
    }
    & > * {
        scrollbar-width: thin;
        scrollbar-color: #e6c899 #000000;
    }
    &::-webkit-scrollbar {
        width: 4px;
    }
    &::-webkit-scrollbar-track {
        background: #000000;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #e6c899;
        border-radius: 11px;
    }
}

.site-main {
    overflow: hidden;
}