%rsp-img {
  max-width: 100%;
  display: block;
  margin: 0;
}

%site-checkbox {
  background-color: #352c25;
  width: 100%;
  height: calc((50 / 1920) * 100vw);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  @include xs {
    height: 8vw;
    &.full-width {
      width: 100%;
    }
  }
  input[type=checkbox] {
    position: absolute;
    left: -9999px;
    box-sizing: border-box;
    padding: 0;
  }
  input[type=checkbox]:not(:checked)+label,
  input[type=checkbox]:checked+label {
    position: relative;
    padding-left: calc((32 / 1920) * 100vw);
    cursor: pointer;
    height: calc((24 / 1920) * 100vw);
    line-height: calc((24 / 1920) * 100vw);
    display: inline-block;
    color: #e6c899;
    font-size: calc((20 / 1920) * 100vw);
    letter-spacing: 0.2rem;
    a {
      color: #e6c899;
      border-bottom: 0.1rem solid #e6c899;
    }
    @include xs {
      font-size: 3.5vw;
      padding-left: 5vw;
    }
  }
  input[type=checkbox]:not(:checked)+label:before,
  input[type=checkbox]:checked+label:before {
    content: '';
    position: absolute;
    left: 0;
    width: calc((25 / 1920) * 100vw);
    height: calc((25 / 1920) * 100vw);
    background-color: #ffffff;
    top: 0;
    box-sizing: border-box;
    border: solid 0.1rem #b2aa94;
    @include xs {
      width: 4vw;
      height: 4vw;
      top: -1.4vw;
    }
  }
  input[type=checkbox]+label:after {
    content: "";
    width: calc((8 / 1920) * 100vw);
    height: calc((6 / 1920) * 100vw);
    border-left: solid 0.2rem #352c25;
    border-bottom: solid 0.2rem #352c25;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate3d(70%, -70%, 0) rotate(-45deg);
    transition: all 300ms ease;
    opacity: 0;
    @include xs {
      width: calc((8 / 414) * 100vw);
      height: calc((6 / 414) * 100vw);
      top: -0.4vw;
      transform: translate3d(0.8vw, -20%, 0) rotate(-45deg);
    }
  }
  input[type=checkbox]:checked+label:after {
    opacity: 1;
  }
}

%site-select {
  box-sizing: border-box;
  height: calc((50 / 1920) * 100vw);
  position: relative;
  overflow: hidden;
  &::before {
    pointer-events: none;
    z-index: 10;
    content: "";
    position: absolute;
    right: calc((16 / 1920) * 100vw);
    top: 50%;
    width: 0; 
    height: 0;
    transform: translate3d(0,-50%,0);
    border-left: calc((10 / 1920) * 100vw) solid transparent;
    border-right: calc((10 / 1920) * 100vw) solid transparent;
    border-top: calc((12 / 1920) * 100vw) solid #e6c899;
    @include xs {
      border-left-width: 2vw;
      border-right-width: 2vw;
      border-top-width: 2.4vw;
      right: 2vw;
    }
  }
  @include xs {
    height: 8vw;
  }
  select {
    box-sizing: border-box;
    background-color: #352c25;
    border: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 100%;
    height: calc((50 / 1920) * 100vw);
    font-size: calc((20 / 1920) * 100vw);
    padding: 0 calc((16 / 1920) * 100vw);
    color: #e6c899;
    transition: all 300ms ease;
    text-align: center;
    option {
      color: #e6c899;
    }
    @include xs {
      height: 8vw;
      font-size: 3.5vw;
      padding-left: 3vw;
      padding-right: 3vw;
    }
  }
}

%site-btn {
  border: 0;
  background-color: transparent;
  display: block;
  padding: 0;
  margin: 0;
  transition: transform 300ms ease;
  img {
    @extend %rsp-img;
  }
  &:hover {
    transform: translate3d(0,-0.4rem,0);
    @include xs {
      transform: translate3d(0,0,0);
    }
  }
}