.float-nav {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate3d(200%,-50%,0);
    z-index: 150;
    display: flex;
    flex-direction: column;
    gap: calc((20 / 1920) * 100vw);
    @include xs {
        display: none;
    }
    &__btn-fb,
    &__btn-yt {
        width: calc((317 / 1920) * 100vw);
        height: calc((191 / 1920) * 100vw);
        position: relative;
        z-index: 0;
        transition: transform 300ms ease;
        &:hover {
            transform: translate3d(0,-0.8rem,0);
        }
        a {
            width: 100%;
            height: 100%;
            z-index: 0;
            display: block;
            position: relative;
            span {
                font-size: calc((18 / 1920) * 100vw);
                position: absolute;
                width: 100%;
                bottom: calc((5 / 1920) * 100vw);
                left: 0;
                color: #fff;
                text-align: center;
                background: linear-gradient(0deg, rgba(140,107,64,1) 0%, rgba(251,228,186,1) 63%, rgba(251,228,186,1) 100%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
        }
    }
    &__btn-fb {
        background: transparent url('../images/float-btn-fb@2x.png') no-repeat 0 0;
        background-size: cover;
    }
    &__btn-yt {
        background: transparent url('../images/float-btn-show@2x.png') no-repeat 0 0;
        background-size: cover;
    }
    &__btn-close {
        font-size: 0;
        width: calc((28 / 1920) * 100vw);
        height: calc((27 / 1920) * 100vw);
        background: transparent url('../images/float-btn-close@3x.png') no-repeat 0 0;
        background-size: cover;
        position: absolute;
        right: calc((60 / 1920) * 100vw);
        top: calc((10 / 1920) * 100vw);
        border: 0;
        z-index: 10;
    }
}