.section-heroes {
    position: relative;
    background: #27271b url('../images/heroes-bg-v2.jpg') no-repeat center top;
    background-size: auto 100%;
    padding-bottom: 50%;
    overflow: hidden;
    @include xs {
        background-image: url('../images/heroes-bg-m-v2@1.5x.jpg');
        padding-bottom: 0;
        min-height: calc(var(--vh, 1vh) * 100);
        background-size: 120% auto;
        background-position: center bottom;
    }
    &__content {
        width: calc((956 / 1920) * 100vw);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-50%,0);
        z-index: 10;
        @include xs {
            width: 100%;
            transform: translate3d(-50%,-10%,0);
        }
    }
    &__logo {
        position: absolute;
        width: calc((623 / 1920) * 100vw);
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-660%,0);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 80vw;
            transform: translate3d(-60%,-760%,0);
        }
    }
    &__main-title {
        width: calc((986 / 1920) * 100vw);
        margin: 0 auto;
        position: relative;
        z-index: 10;
        transform: translate3d(0, -230%, 0);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 100vw;
            transform: translate3d(0, -330%, 0);
            margin-bottom: calc((40 / 414) * 55vw);
        }
    }
    &__table {
        width: calc((727 / 1920) * 100vw);
        position: absolute;
        transform: translate3d(-36.5%, -38.2%, 0);
        left: 50%;
        top: 50%;
        z-index: 0;
        img {
            @extend %rsp-img;
            position: relative;
            z-index: 0;
        }
        @include xs {
            width: 80vw;
            transform: translate3d(-50%, -50%, 0);
        }
    }
    &__inner {
        display: flex;
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 5;
        transform: translate3d(-50%,-39%,0);
        gap: calc((40 / 1920) * 100vw);
        @include xs {
            gap: calc((40 / 414) * 50vw);
            transform: translate3d(-50%,-32%,0);
        }
    }
    &__column {
        display: flex;
        flex-direction: column;
        gap: calc((22 / 1920) * 100vw);
        @include xs {
            gap: calc((22 / 414) * 50vw);
        }
    }
    &__title {
        cursor: pointer;
        width: calc((232 / 1920) * 100vw);
        height: calc((41 / 1920) * 100vw);
        background: transparent url('../images/heroes-title-bg@2x.png') no-repeat center top;
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: calc((24 / 1920) * 100vw);
        font-weight: 900;
        color: #fff;
        transform: translate3d(0,0,0,) scale(1);
        transition: transform 300ms ease;
        @include xs {
            width: calc((232 / 414) * 50vw);
            height: calc((41 / 414) * 50vw);
            font-size: calc((24 / 414) * 50vw);
            animation-name: light;
            animation-play-state: running;
            animation-direction: alternate;
            animation-fill-mode: both;
            animation-duration: 0.3s;
            animation-timing-function: ease-in-out;
            animation-iteration-count: infinite;
            animation-duration: 1.5s;
            &:nth-child(1) {
                animation-delay: 0;
            }
            &:nth-child(2) {
                animation-delay: 0.3s;
            }
            &:nth-child(3) {
                animation-delay: 0.6s;
            }
            &:nth-child(4) {
                animation-delay: 0.9s;
            }
            &:nth-child(5) {
                animation-delay: 1.2s;
            }
        }
        &:hover {
            transform: translate3d(0,0,0,) scale(1.2);
            @include xs {
                animation-play-state: paused;
            }
        }
    }
    &__text {
        text-align: center;
        color: #ffcf70;
        font-size: calc((13 / 1920) * 100vw);
        text-shadow: 0 0 8px rgba(0, 0, 0, 0.88), 0 0 12px rgba(0, 0, 0, 0.88), 0 0 24px rgba(0, 0, 0, 0.88);
        @include xs {
            font-size: 2vw;
        }
    }
    &__footer {
        z-index: 20;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,270%,0);
        @include xs {
            transform: translate3d(-50%,380%,0);
        }
    }
    &__btn-check {
        transform: translate3d(0,0,0);
        display: block;
        width: calc((541 / 1920) * 100vw);
        height: calc((106 / 1920) * 100vw);
        background: transparent url('../images/heroes-btn-check-v2@2x.png') no-repeat center top;
        background-size: cover;
        margin: 0 auto;
        font-size: 0;
        transform: translate3d(0,0,0);
        position: relative;
        transition: transform 300ms ease;
        &:hover {
            transform: translate3d(0,5%,0);
        }
        @include xs {
            width: calc((541 / 414) * 50vw);
            height: calc((106 / 414) * 50vw);
            transform: translate3d(0,0,0);
            &:hover {
                transform: translate3d(0,0,0);
            }
        }
    }
    &__gift {
        position: absolute;
        width: 54vw;
        left: 50%;
        bottom: 0;
        transform: translate3d(-3%, 130%, 0);
        display: none;
        z-index: 10;
        img {
            @extend %rsp-img;
        }
        @include xs {
            display: block;
        }
    }
    &__cht {
        position: absolute;
        width: 55vw;
        left: 50%;
        bottom: 0;
        transform: translate3d(-90%, 134%, 0);
        display: none;
        z-index: 10;
        img {
            @extend %rsp-img;
        }
        @include xs {
            display: block;
        }
    }
}

@keyframes scale {
    0% {
        transform: translate3d(0,0,0) scale(1);
    }
    50% {
        transform: translate3d(0,0,0) scale(1.1);
    }
    100% {
        transform: translate3d(0,0,0) scale(1);
    }
}

@keyframes light {
    0% {
        filter: brightness(1);
    }
    50% {
        filter: brightness(1.5);
    }
    100% {
        filter: brightness(1);
    }
}