.section-event {
    position: relative;
    background: #000 url('../images/event-bg.jpg') no-repeat center top;
    background-size: auto 100%;
    padding-bottom: 50%;
    overflow: hidden;
    @include xs {
        background-image: url('../images/event-bg-m-v2.jpg');
        padding-bottom: 0;
        min-height: calc(var(--vh, 1vh) * 100);
        background-size: 100% auto;
    }
    &__content {
        width: calc((956 / 1920) * 100vw);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-50%,0);
        z-index: 10;
        @include xs {
            width: 100%;
            transform: translate3d(-50%,-140%,0);
        }
    }
    &__main-title {
        width: calc((802 / 1920) * 100vw);
        margin: 0 auto calc((24 / 1920) * 100vw);
        position: relative;
        z-index: 10;
        padding-bottom: calc((500 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 100vw;
            padding-bottom: calc((100 / 414) * 100vw);
        }
    }
    &__item-wrap {
        display: flex;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-36%,0);
        @include xs {
            flex-direction: column;
            transform: translate3d(-50%,-1%,0);
        }
    }
    &__item {
        width: calc((455 / 1920) * 100vw);
        @include xs {
            width: 48vw;
        }
        img {
            @extend %rsp-img;
        }
    }
    &__text {
        text-align: center;
        color: #ffcf70;
        font-size: calc((13 / 1920) * 100vw);
        text-shadow: 0 0 8px rgba(0, 0, 0, 0.88), 0 0 12px rgba(0, 0, 0, 0.88), 0 0 24px rgba(0, 0, 0, 0.88);
        @include xs {
            font-size: 2vw;
        }
    }
    &__footer {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,140%,0);
        @include xs {
            transform: translate3d(-50%,60%,0);
        }
    }
    &__btn-event {
        width: calc((541 / 1920) * 100vw);
        height: calc((106 / 1920) * 100vw);
        background: transparent url('../images/event-btn-event-v2@2x.png') no-repeat center bottom;
        background-size: auto 100%;
        display: flex;
        margin: calc((60 / 1920) * 100vw) auto 0;
        border: 0;
        padding: 0;
        font-size: 0;
        position: relative;
        z-index: 10;
        transition: transform 300ms ease;
        &:hover {
            transform: translate3d(0,5%,0);
        }
        @include xs {
            width: calc((541 / 414) * 50vw);
            height: calc((106 / 414) * 50vw);
            margin-top: calc((500 / 414) * 50vw);
            &:hover {
                transform: translate3d(0,0,0);
            }
        }
    }
}